import React from 'react'
import {uuid} from 'utils'

// 
export interface IMenu {
  id: string,
  name: string
  children?: IMenu[]
}

export interface INav {
  name: string
  menus: IMenu[]
}

export interface IState {
  navs: INav[]
  checkedNav: INav
  checkedMenu: IMenu
}

// 
export enum ActionType {
  NAV_CLICK,
  MENU_CLICK
}

export interface IAction {
  type: ActionType
  payload?: INav | IMenu
}


// 
export interface IContext {
  state: IState,
  dispatch: (action: IAction) => void
}

export const LayoutContext = React.createContext<IContext | null>(null!)

// 
export const initState = (): IState => {
  const navs: INav[] = [
    {
      name: 'Linux',
      menus: [
        {
          id: uuid(),
          name: '',
          children: [
            {
              id: uuid(),
              name: 'cd'
            },
            {
              id: uuid(),
              name: 'pwd'
            }
          ]
        },
        {
          id: uuid(),
          name: 'Shell',
          children: [
            {
              id: uuid(),
              name: 'for'
            },
            {
              id: uuid(),
              name: 'if'
            }
          ]
        }
      ]
    },
    {
      name: 'Java',
      menus: [
        {
          id: uuid(),
          name: 'JDK',
          children: [
            {
              id: uuid(),
              name: ''
            },
            {
              id: uuid(),
              name: ''
            }
          ]
        }
      ]
    }
  ]
  const checkedNav: INav = navs[0]
  const checkedMenu: IMenu = checkedNav.menus[0]

  return {
    navs,
    checkedNav,
    checkedMenu
  }
}
